/*Input Elements' Params*/
input, textarea {
  background-color: white;
  color: black;
  border: 1px solid black;
  border-radius: initial;
  padding: 0 .5em; }

input, textarea {
  width: 100%; }

textarea {
  resize: vertical; }

input[type="submit"],
input[type="button"] {
  background-color: black;
  display: block; }

input[type="date"],
input[type="time"],
input[type="submit"],
input[type="button"] {
  width: auto; }

input::-webkit-datetime-edit {
  color: black; }

input::-webkit-inner-spin-button,
input::-webkit-calendar-picker-indicator {
  cursor: pointer;
  opacity: 1; }

input[type="checkbox"],
input[type="radio"] {
  cursor: pointer;
  padding: 0;
  height: 1rem;
  width: 1rem;
  vertical-align: text-top; }
  input[type="checkbox"]:checked,
  input[type="radio"]:checked {
    background-color: black; }

input[type="radio"] {
  border-radius: 100%; }

select {
  cursor: pointer; }

input:focus, select:focus, textarea:focus, button:focus {
  outline: none; }

::-webkit-input-placeholder {
  color: darkgrey; }

:-moz-placeholder {
  color: darkgrey; }

/* Firefox 18- */
::-moz-placeholder {
  color: darkgrey; }

/* Firefox 19+ */
:-ms-input-placeholder {
  color: darkgrey; }

